import Api from '../../api/user';
	
 export default {
        data() {
            return {
				loginForm: {  
				    workNo: "05360",  
				    password: "123456",  
				}, 
            }
        },
        methods: {
            login: function(e) {
				uni.showLoading({
				    title: '登录中',	
				})
				Api.login(e.detail.value)
				.then(res => {
					uni.hideLoading()
					if(res.code==200){
						wx.showToast({
							icon:'success',
							mask:true,
							title: '登录成功',
							duration: 2000
						})
					}
					uni.setStorageSync('token', res.data.token);
					uni.switchTab({
						url: '/pages/info/info'
					});
				})
            }
        }
    }
